import React, { useState, useEffect } from "react";
import SearchCard from "../components/SearchCard";
import { Link } from "react-router-dom";

function Search() {
  const [search_result, setSearchResult] = useState({});
  const [page, setPage] = useState(1);
  const [search, setSearch] = useState("");

  const banned_type = [
    "Bookmarks",
    "Lunchboxes",
    "PopSockets",
    "Glasses & Barware",
    "Card Games",
    "Mugs",
    "Pins & Badges",
    "Pens",
    "Comics & Graphic Novels",
    "Figures & Toy Soldiers",
    "Plush Toys",
    "Uniform Patches",
    "Masks",
    "Posters & Prints",
    "Figure & Toy Soldier Sets",
    "Whatever Else",
    "Brochures & Catalogs",
    "Funko Mini Moments",
    "Backpacks, Luggage & Purses",
    "Christmas & Holiday Ornaments"
  ];
  const banned_series = [
    "Funko Articulated Action Figures",
    "Paka Paka",
    "Funko Minis",
    "Pint Size Heroes",
    "Mystery Minis",
    "Soda Figures",
    "Popsies",
    "Vinyl Sugar",
    "Pop! Mask",
    "Funko Prototypes",
    "MyMoji",
    "Wacky Wobblers"
  ];

  function setSearchPls(e) {
    if (event.key != "Enter") return;
    setSearchResult(0);
    setSearch(
      event.target?.value
        .split(" ")
        .join("+")
        .toLowerCase()
    );
    setPage(1);
  }

  function searchNewPagePls() {
    let new_page = page + 1;
    setPage(new_page);
  }

  useEffect(
    () => {
      if (search == "") return;
      let url =
        "https://www.hobbydb.com/api/catalog_items?include_last_page=true&include_main_images=true&market_id=poppriceguide&page=" +
        page +
        "&per=50&q=" +
        search +
        "&serializer=CatalogItemPudbSerializer&subvariants=true";
      fetch(url)
        .then(response => response.json())
        .then(obj => {
          let temp_obj = {};
          temp_obj = obj.data.filter(
            result =>
              !banned_type.includes(result.attributes.catalog_item_type_name) &&
              !banned_series.includes(result.attributes.series[0]?.name)
          );

          if (search_result.data) {
            let old_data = search_result.data;
            setSearchResult({
              data: old_data.concat(temp_obj),
              lastPage: obj.meta.lastPage
            });
          } else {
            setSearchResult({ data: temp_obj, lastPage: obj.meta.lastPage });
          }
        });
    },
    [search, page]
  );

  return (
    <div id="search">
      <input
        type="textbox"
        onKeyUp={e => {
          setSearchPls(e);
        }}
        placeholder="Recherche ..."
      />

      {search_result?.data?.length > 0 ? (
        search_result.data.map((result, i) => {
          return (
            <a
              href={
                "https://www.hobbydb.com/marketplaces/poppriceguide/catalog_items/" +
                result.id
              }
              target="_blank"
              key={"search_" + i}
            >
              <SearchCard pop={result} />
            </a>
          );
        })
      ) : search_result?.data ? (
        <p>Aucun résultat</p>
      ) : search_result == 0 && search != "" ? (
        <p>Recherche ... </p>
      ) : (
        ""
      )}

      {search_result?.data?.length > 0 && !search_result.lastPage ? (
        <button onClick={searchNewPagePls}>Chargez plus</button>
      ) : (
        ""
      )}
    </div>
  );
}

export default Search;
