import VanillaTilt from "vanilla-tilt";
import React, { useEffect } from "react";

function PopCard(props) {
  useEffect(() => {
    VanillaTilt.init(document.querySelectorAll(".tiltcard"), {
      reverse: true,
      reset: true,
      glare: true,
      scale: 1.05,
      "max-glare": 0.1,
      "max-glare": 0.25,
      "max-glare": 0.33,
      max: 5
    });
  }, []);

  const pop = props.pop;
  const convertPopToIdPls = window.$convertPopToIdPls;
  const convertToUrlPls = window.$convertToUrlPls;
  const pop_id = convertPopToIdPls(pop);

  let stickers = [];
  stickers = pop.features?.map(feature => {
    if (feature == "exclusive" && pop.exclusive && pop.exclusive != "none") {
      let exc = "exc_";
      if (pop.exclusive == "special edition") exc = "";
      return (
        "https://raw.githubusercontent.com/Yxmna/funko-stickers/main/svg/" +
        exc +
        pop.exclusive.split(" ").join("_") +
        ".svg"
      );
    } else if (feature == "event" && pop.event) {
      return (
        "https://raw.githubusercontent.com/Yxmna/funko-stickers/main/svg/exc_" +
        pop.event.split(" ").join("_") +
        ".svg"
      );
    } else {
      return (
        "https://raw.githubusercontent.com/Yxmna/funko-stickers/main/svg/" +
        feature.split(" ").join("_") +
        ".svg"
      );
    }
  });

  return (
    <article className={"popcard" + (pop.ok ? " popok" : " popnotok")}>
      <div className="popimage">
        <div>
          <img
            className="popshadow"
            src={
              "https://github.com/Yxmna/funko-collection-source-code/raw/main/public/img/" +
              pop_id +
              ".png"
            }
            onError={e => {
              e.target.src =
                "https://cdn.discordapp.com/attachments/962465593116733511/976157974915608647/missing_img.png";
              e.target.style.opacity = "0.5";
            }}
          />
        </div>
        <img
          src={
            "https://github.com/Yxmna/funko-collection-source-code/raw/main/public/img/" +
            pop_id +
            ".png"
          }
          onError={e => {
            e.target.src =
              "https://cdn.discordapp.com/attachments/962465593116733511/976157974915608647/missing_img.png";
            e.target.style.opacity = "0.5";
            e.target.style.filter = "brightness(100%)";
          }}
        />
        {pop.more_details?.includes("love") ? (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            x="0px"
            y="0px"
            viewBox="0 0 472.7 472.7"
          >
            <g>
              <path
                id="XMLID_810_"
                d="M433.5,67c-25.3-25.3-59-39.3-94.8-39.3s-69.6,14-94.9,39.4l-7.3,7.3l-7.5-7.5   c-25.4-25.4-59.1-39.4-95-39.4c-35.8,0-69.4,13.9-94.7,39.3C13.9,92.2,0,125.9,0,161.7s14,69.5,39.4,94.8l182.7,182.7   c3.8,3.8,9,6,14.5,6c5.4,0,10.6-2.2,14.5-6l182.2-182.4c25.4-25.4,39.3-59.1,39.4-94.9S458.8,92.4,433.5,67z M132.5,117.2"
              />
            </g>
          </svg>
        ) : (
          ""
        )}
      </div>
      <div
        data-tilt
        className={
          pop.more_details?.includes("love") ? "tiltcard love" : "tiltcard"
        }
      >
        <h1>
          {pop?.name} <span>{pop?.details}</span>
        </h1>

        {pop.estimated || pop.info?.price ? (
          <p>
            {pop.info?.price ? (
              <span className="price">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  x="0px"
                  y="0px"
                  viewBox="0 0 17.837 17.837"
                >
                  <g>
                    <path d="M16.145,2.571c-0.272-0.273-0.718-0.273-0.99,0L6.92,10.804l-4.241-4.27   c-0.272-0.274-0.715-0.274-0.989,0L0.204,8.019c-0.272,0.271-0.272,0.717,0,0.99l6.217,6.258c0.272,0.271,0.715,0.271,0.99,0   L17.63,5.047c0.276-0.273,0.276-0.72,0-0.994L16.145,2.571z" />
                  </g>
                </svg>
                {pop.info?.price}

                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  x="0px"
                  y="0px"
                  viewBox="0 0 481.569 481.569"
                >
                  <g>
                    <path d="M444.288,429.288c-5.4-13.2-10.9-26.4-16.9-39.4c-5.3-11.6-12.1-15-24.8-12.1c-16.1,3.7-31.9,8.6-48,11.9   c-31.1,6.5-62.3,7.1-93-2.6c-38.5-12.1-59-40-71.6-76h104.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-114.4   c0-9.2-0.1-18,0-26.8h114.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-100c0-0.4,0-0.8,0.2-1   c12-27.3,29.5-49.2,58.2-60.6c33.4-13.2,67.5-12.9,101.9-5.8c16.3,3.3,32.3,8.3,48.6,12c11.9,2.7,18.8-0.8,23.9-11.9   c5.9-12.8,11.3-25.8,16.7-38.9c5.1-12.3,2.1-21-9.5-27.8c-2.9-1.7-5.9-3.1-9-4.3c-48.2-18.8-97.9-25.8-149.2-17.6   c-36.1,5.8-69.8,18.2-98.9,40.8c-36.7,28.4-60.5,65.9-74.3,110l-1.7,5.1h-51.4c-8.2,0-14.8,6.6-14.8,14.8v32.9   c0,8.2,6.6,14.8,14.8,14.8h40.5c0,9,0,17.7,0,26.8h-40.5c-8.2,0-14.8,6.6-14.8,14.8v32.9c0,8.2,6.6,14.8,14.8,14.8h48.8   c3.7,12,6.8,24.2,11.5,35.7c24.7,59.6,66.1,102,128.4,122.2c51.5,16.7,103.4,16.2,155.3,1.9c13.5-3.7,26.9-8.5,39.7-14.4   C445.988,450.788,449.188,441.188,444.288,429.288z" />
                  </g>
                  <script xmlns="" />
                </svg>
              </span>
            ) : (
              ""
            )}

            {pop.estimated > 0 ? (
              <span className="estimated">
                Estimée à {Math.floor(pop.estimated / window.$rate)}
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  x="0px"
                  y="0px"
                  viewBox="0 0 481.569 481.569"
                >
                  <g>
                    <path d="M444.288,429.288c-5.4-13.2-10.9-26.4-16.9-39.4c-5.3-11.6-12.1-15-24.8-12.1c-16.1,3.7-31.9,8.6-48,11.9   c-31.1,6.5-62.3,7.1-93-2.6c-38.5-12.1-59-40-71.6-76h104.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-114.4   c0-9.2-0.1-18,0-26.8h114.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-100c0-0.4,0-0.8,0.2-1   c12-27.3,29.5-49.2,58.2-60.6c33.4-13.2,67.5-12.9,101.9-5.8c16.3,3.3,32.3,8.3,48.6,12c11.9,2.7,18.8-0.8,23.9-11.9   c5.9-12.8,11.3-25.8,16.7-38.9c5.1-12.3,2.1-21-9.5-27.8c-2.9-1.7-5.9-3.1-9-4.3c-48.2-18.8-97.9-25.8-149.2-17.6   c-36.1,5.8-69.8,18.2-98.9,40.8c-36.7,28.4-60.5,65.9-74.3,110l-1.7,5.1h-51.4c-8.2,0-14.8,6.6-14.8,14.8v32.9   c0,8.2,6.6,14.8,14.8,14.8h40.5c0,9,0,17.7,0,26.8h-40.5c-8.2,0-14.8,6.6-14.8,14.8v32.9c0,8.2,6.6,14.8,14.8,14.8h48.8   c3.7,12,6.8,24.2,11.5,35.7c24.7,59.6,66.1,102,128.4,122.2c51.5,16.7,103.4,16.2,155.3,1.9c13.5-3.7,26.9-8.5,39.7-14.4   C445.988,450.788,449.188,441.188,444.288,429.288z" />
                  </g>
                  <script xmlns="" />
                </svg>
              </span>
            ) : (
              ""
            )}
          </p>
        ) : (
          ""
        )}

        {pop.features ? (
          <div
            className={
              pop.features.length <= 3 ? "stickers" : "stickers minimal"
            }
          >
            {stickers?.map((sticker, i) => (
              <img
                key={"sticker_" + i}
                src={sticker}
                onError={e => {
                  e.target.src =
                    "https://media.discordapp.net/attachments/962465593116733511/973529042710040597/sticker_error.png";
                }}
              />
            ))}
          </div>
        ) : (
          ""
        )}
      </div>
    </article>
  );
}

export default PopCard;
