import VanillaTilt from "vanilla-tilt";
import React, { useEffect } from "react";

function CollectionCard({ collection, pops }) {
  useEffect(() => {
    VanillaTilt.init(document.querySelectorAll(".tiltcard"), {
      reverse: true,
      reset: true,
      glare: true,
      scale: 1.025,
      "max-glare": 0.33,
      max: 2
    });
  }, []);

  let randoms = [];
  let max_random = pops.length;
  if (max_random > 6) max_random = 6;

  for (var i = 0; i < max_random; i++) {
    let r = 0;
    do {
      r = Math.floor(Math.random() * pops.length);
    } while (
      randoms
        .join("-")
        .toString()
        .includes(r.toString())
    );
    randoms.push(r);
  }

  const convertPopToIdPls = window.$convertPopToIdPls;
  const convertToUrlPls = window.$convertToUrlPls;

  return (
    <article className="collectioncard">
      <div className="pops">
        <div>
          {randoms.map((r, i) => (
            <img
              key={"collectionimg_" + i}
              className="popshadow"
              src={
                "https://github.com/Yxmna/funko-collection-source-code/raw/main/public/img/" +
                convertPopToIdPls(pops[r]) +
                ".png"
              }
              onError={e => {
                e.target.src =
                  "https://cdn.discordapp.com/attachments/962465593116733511/976157974915608647/missing_img.png";
                e.target.style.opacity = "0.5";
              }}
            />
          ))}
        </div>
        {randoms.map((r, i) => (
          <img
            key={"collectionimg_" + i}
            src={
              "https://github.com/Yxmna/funko-collection-source-code/raw/main/public/img/" +
              convertPopToIdPls(pops[r]) +
              ".png"
            }
            onError={e => {
              e.target.src =
                "https://cdn.discordapp.com/attachments/962465593116733511/976157974915608647/missing_img.png";
              e.target.style.opacity = "0.5";
            }}
          />
        ))}
      </div>
      <div data-tilt className="tiltcard">
        <div className="collectioninfo">
          <p>{collection?.name.toUpperCase()}</p>
          <p>{collection?.description}</p>
        </div>
      </div>
    </article>
  );
}

export default CollectionCard;
