import React, { useEffect, useState } from "react";
import PopCard from "../components/PopCard";
import { useParams } from "react-router-dom";
import PopGraph from "../components/PopGraph";
import Card from "../components/Card";
import { Link } from "react-router-dom";

function Pop(props) {
  let params = useParams();
  const convertPopToIdPls = window.$convertPopToIdPls;


  let pop = {};
  pop = props.data.find(pop => {
    let pop_test = convertPopToIdPls(pop, true);
    return pop_test == params.pop_id;
  });

  const [chart_labels, setChartLabels] = useState([]);
  const [chart_data, setChartData] = useState([]);

  const [hbdb_data, setHbdbData] = useState([]);
  const [hbdb_imgs, setHbdbImgs] = useState([]);
  const [img_index, setImgIndex] = useState(0);

  useEffect(
    () => {
      let url =
        "https://www.hobbydb.com/api/historical_ci_values?catalog_item_id=" +
        pop?.hdbid;
      fetch(url)
        .then(function(res) {
          return res.json();
        })
        .then(function(obj) {
          obj = obj.data
            .filter(price => price.attributes.dateEnd)
            .sort((price1, price2) => {
              let date1 = new Date(price1.attributes.dateEnd);
              let date2 = new Date(price2.attributes.dateEnd);
              return date1 - date2;
            });
          let labels = obj;
          labels = labels.map(price => {
            let date = new Date(price.attributes.dateEnd);
            return date.toLocaleDateString();
          });
          setChartLabels(labels);
          let data = obj;
          data = data.map(price => price.attributes.value);
          setChartData(data);
        });
    },
    [pop?.hdbid]
  );

  useEffect(
    () => {
      if (!pop?.hdbid) return;
      let url =
        "https://www.hobbydb.com/api/catalog_items/" +
        pop.hdbid +
        "?fields=true&related_subjects=true&market_id=poppriceguide";
      fetch(url)
        .then(function(res) {
          return res.json();
        })
        .then(function(obj) {
          let temp = obj.data[0].attributes;
          if (temp.productionFields?.find(f => f.name == "Produced")) {
            const months = [
              "Janvier",
              "Février",
              "Mars",
              "Avril",
              "Mai",
              "Juin",
              "Juillet",
              "Août",
              "Septembre",
              "Octobre",
              "Novembre",
              "Décembre"
            ];
            let date_data = temp.productionFields?.find(
              f => f.name == "Produced"
            )?.data[0].value;
            let date;
            if (date_data.split(" ").length > 3) {
              date = new Date(
                date_data
                  .split(":")[1]
                  .split(",")
                  .join("")
                  .split("th")
                  .join("")
                  .split("st")
                  .join("")
                  .split("nd")
                  .join("")
                  .split("rd")
                  .join("")
              );
              temp["date"] =
                date.getDay() +
                " " +
                months[date.getMonth()] +
                " " +
                date.getFullYear();
            } else if (date_data.split(" ").length > 2) {
              date = new Date("01 " + date_data.split(":")[1]);
              temp["date"] = months[date.getMonth()] + " " + date.getFullYear();
            } else {
              date = new Date("01 01" + date_data.split(":")[1]);
              temp["date"] = date.getFullYear();
            }
          }
          if (obj.data[0]) setHbdbData(temp);
        });
      },
    [pop?.hdbid]
  );

  useEffect(
    () => {
      let url =
        "https://www.hobbydb.com/api/catalog_items/" + pop?.hdbid + "/images";
      fetch(url)
        .then(function(res) {
          return res.json();
        })
        .then(function(obj) {
          setHbdbImgs(obj.photos.map(img => img.url));
        });
    },
    [pop?.hdbid]
  );

  function switchImgsPls() {
    if (img_index == hbdb_imgs.length - 1 || hbdb_imgs.length == 0) {
      setImgIndex(0);
    } else {
      setImgIndex(index => index + 1);
    }
  }

  let stickers = [];
  stickers = pop?.features?.map(feature => {
    if (feature == "exclusive" && pop.exclusive && pop.exclusive != "none") {
      let exc = "exc_";
      if (pop.exclusive == "special edition") exc = "";
      return {
        name: "exclusive",
        detail: pop.exclusive,
        link:
          "https://raw.githubusercontent.com/Yxmna/funko-stickers/main/svg/" +
          exc +
          pop.exclusive.split(" ").join("_") +
          ".svg"
      };
    } else if (feature == "event" && pop.event) {
      return {
        name: "event",
        detail: pop.event,
        link:
          "https://raw.githubusercontent.com/Yxmna/funko-stickers/main/svg/exc_" +
          pop.event.split(" ").join("_") +
          ".svg"
      };
    } else {
      return {
        name: feature,
        detail: "",
        link:
          "https://raw.githubusercontent.com/Yxmna/funko-stickers/main/svg/" +
          feature.split(" ").join("_") +
          ".svg"
      };
    }
  });

  const city_imgs = {
    internet: "https://www.svgrepo.com/show/294179/internet.svg",
    "salon-de-provence":
      "https://upload.wikimedia.org/wikipedia/commons/thumb/1/1a/Blason_de_la_ville_de_Salon-de-Provence_%2813%29.svg/800px-Blason_de_la_ville_de_Salon-de-Provence_%2813%29.svg.png",
    verdun:
      "https://upload.wikimedia.org/wikipedia/commons/thumb/c/c2/Blason_Verdun_55.svg/800px-Blason_Verdun_55.svg.png",
    marseille:
      "https://upload.wikimedia.org/wikipedia/commons/thumb/8/83/Armoiries_de_Marseille_Blason2.svg/800px-Armoiries_de_Marseille_Blason2.svg.png",
    "aix-en-provence":
      "https://upload.wikimedia.org/wikipedia/commons/thumb/5/52/Escut_de_Ais_de_Proven%C3%A7a.svg/800px-Escut_de_Ais_de_Proven%C3%A7a.svg.png"
  };

  const shop_imgs = {
    fnac:
      "https://media.discordapp.net/attachments/977856805231280208/977857826762403900/fnac.png?width=585&height=603",
    micromania:
      "https://www.micromania.fr/on/demandware.static/Sites-Micromania-Site/-/default/dwb32d5038/images/favicons/favicon-196x196.png",
    cora: "https://upload.wikimedia.org/wikipedia/commons/c/ce/Cora_logo.svg",
    "la cité magique": "https://lacitemagique.com/img/favicon.ico?1615475740",
    "pop in a box":
      "https://media.discordapp.net/attachments/977856805231280208/977856888643391498/pop_in_a_box.png",
    popcultcha:
      "https://media.discordapp.net/attachments/977856805231280208/977858206061723688/popcultcha.png",
    "cash express":
      "https://pbs.twimg.com/profile_images/1471502732607459340/OodjPqmQ_400x400.jpg",
    "cash converters":
      "https://media.discordapp.net/attachments/977856805231280208/977858660866879498/cash_converter.png",
    "chalice collectibles":
      "https://media.discordapp.net/attachments/977856805231280208/977856887758401646/chalice_collectibles.png",
    gifi:
      "https://media.discordapp.net/attachments/977856805231280208/977856888433692682/gifi.png",
    noz:
      "https://upload.wikimedia.org/wikipedia/commons/thumb/a/ab/LOGO_NOZ.png/1920px-LOGO_NOZ.png",
    vinted:
      "https://media.discordapp.net/attachments/977856805231280208/977856889083801620/vinted.png",
    "art'n magic":
      "https://scontent-mrs2-1.xx.fbcdn.net/v/t1.6435-9/45625338_1978544442210818_7174991142847512576_n.jpg?_nc_cat=100&ccb=1-7&_nc_sid=09cbfe&_nc_ohc=uxWt5KEoCb8AX9uVhCe&_nc_ht=scontent-mrs2-1.xx&oh=00_AT9bxWa6FXcTIoVyplf_UhKp4QaxnQnUfvsnSg9OXIDPRw&oe=62AE48BB",
    amazon:
      "https://media.discordapp.net/attachments/977856805231280208/977856887477395456/amazon.png",
    toystops:
      "https://media.discordapp.net/attachments/977856805231280208/977856888853102612/toystops.png"
  };

  return (
    <div id="pop">
      <div>
        <div className="popview">
          <Card
            tilt={0.5}
            glare={0.2}
            scale={1.01}
            notilt={
              <>
                <div>
                  <img
                    className="popshadow"
                    src={
                      "https://github.com/Yxmna/funko-collection-source-code/raw/main/public/img/" +
                      convertPopToIdPls(pop) +
                      ".png"
                    }
                    onError={e => {
                      e.target.src =
                        "https://cdn.discordapp.com/attachments/962465593116733511/976157974915608647/missing_img.png";
                      e.target.style.opacity = "0.5";
                    }}
                  />
                </div>
                <img
                  src={
                    "https://github.com/Yxmna/funko-collection-source-code/raw/main/public/img/" +
                    convertPopToIdPls(pop) +
                    ".png"
                  }
                  onError={e => {
                    e.target.src =
                      "https://cdn.discordapp.com/attachments/962465593116733511/976157974915608647/missing_img.png";
                    e.target.style.opacity = "0.5";
                    e.target.style.filter = "brightness(100%)";
                  }}
                />
              </>
            }
          >
            <h1>
              {pop?.name} <span>{pop?.details}</span>
            </h1>
            <p>{pop?.series + " - " + pop?.number}</p>
          </Card>
        </div>

        <div className="popstickers">
          <Card tilt={0.5} glare={0.2} scale={1.01}>
            {stickers?.length > 0
              ? stickers?.map((sticker, i) => (
                  <div>
                    <img
                      key={"sticker_" + i}
                      src={sticker.link}
                      onError={e => {
                        e.target.src =
                          "https://media.discordapp.net/attachments/962465593116733511/973529042710040597/sticker_error.png";
                      }}
                    />
                    <h1 key={"stickername_" + i}>{sticker.name}</h1>
                    {sticker.detail != "" ? <p>{sticker.detail}</p> : ""}
                  </div>
                ))
              : "Pas de particularité"}
          </Card>
        </div>
      </div>

      <div>
        {pop?.info || hbdb_data.productionFields ? (
          <div className="popinfo">
            <Card tilt={0.5} glare={0.2} scale={1.01}>
              {pop.info ? (
                <ul>
                  {pop?.info?.city ? (
                    <li>
                      {" "}
                      Lieux:{" "}
                      <img src={city_imgs[pop.info.city.toLowerCase()]} />{" "}
                      <span>{pop.info.city}</span>
                    </li>
                  ) : (
                    ""
                  )}
                  {pop?.info?.shop ? (
                    <li>
                      Magasin:{" "}
                      <img src={shop_imgs[pop.info.shop.toLowerCase()]} />{" "}
                      <span>{pop.info.shop}</span>
                    </li>
                  ) : (
                    ""
                  )}
                  {pop?.info?.price ? (
                    <li>
                      Prix: <span>{pop.info.price}</span>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        x="0px"
                        y="0px"
                        viewBox="0 0 481.569 481.569"
                      >
                        <g>
                          <path d="M444.288,429.288c-5.4-13.2-10.9-26.4-16.9-39.4c-5.3-11.6-12.1-15-24.8-12.1c-16.1,3.7-31.9,8.6-48,11.9   c-31.1,6.5-62.3,7.1-93-2.6c-38.5-12.1-59-40-71.6-76h104.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-114.4   c0-9.2-0.1-18,0-26.8h114.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-100c0-0.4,0-0.8,0.2-1   c12-27.3,29.5-49.2,58.2-60.6c33.4-13.2,67.5-12.9,101.9-5.8c16.3,3.3,32.3,8.3,48.6,12c11.9,2.7,18.8-0.8,23.9-11.9   c5.9-12.8,11.3-25.8,16.7-38.9c5.1-12.3,2.1-21-9.5-27.8c-2.9-1.7-5.9-3.1-9-4.3c-48.2-18.8-97.9-25.8-149.2-17.6   c-36.1,5.8-69.8,18.2-98.9,40.8c-36.7,28.4-60.5,65.9-74.3,110l-1.7,5.1h-51.4c-8.2,0-14.8,6.6-14.8,14.8v32.9   c0,8.2,6.6,14.8,14.8,14.8h40.5c0,9,0,17.7,0,26.8h-40.5c-8.2,0-14.8,6.6-14.8,14.8v32.9c0,8.2,6.6,14.8,14.8,14.8h48.8   c3.7,12,6.8,24.2,11.5,35.7c24.7,59.6,66.1,102,128.4,122.2c51.5,16.7,103.4,16.2,155.3,1.9c13.5-3.7,26.9-8.5,39.7-14.4   C445.988,450.788,449.188,441.188,444.288,429.288z" />
                        </g>
                        <script xmlns="" />
                      </svg>
                    </li>
                  ) : (
                    ""
                  )}
                  {pop?.estimated > 0 ? (
                    <li>
                      Valeur:{" "}
                      <span>{Math.floor(pop.estimated / window.$rate)}</span>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        x="0px"
                        y="0px"
                        viewBox="0 0 481.569 481.569"
                      >
                        <g>
                          <path d="M444.288,429.288c-5.4-13.2-10.9-26.4-16.9-39.4c-5.3-11.6-12.1-15-24.8-12.1c-16.1,3.7-31.9,8.6-48,11.9   c-31.1,6.5-62.3,7.1-93-2.6c-38.5-12.1-59-40-71.6-76h104.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-114.4   c0-9.2-0.1-18,0-26.8h114.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-100c0-0.4,0-0.8,0.2-1   c12-27.3,29.5-49.2,58.2-60.6c33.4-13.2,67.5-12.9,101.9-5.8c16.3,3.3,32.3,8.3,48.6,12c11.9,2.7,18.8-0.8,23.9-11.9   c5.9-12.8,11.3-25.8,16.7-38.9c5.1-12.3,2.1-21-9.5-27.8c-2.9-1.7-5.9-3.1-9-4.3c-48.2-18.8-97.9-25.8-149.2-17.6   c-36.1,5.8-69.8,18.2-98.9,40.8c-36.7,28.4-60.5,65.9-74.3,110l-1.7,5.1h-51.4c-8.2,0-14.8,6.6-14.8,14.8v32.9   c0,8.2,6.6,14.8,14.8,14.8h40.5c0,9,0,17.7,0,26.8h-40.5c-8.2,0-14.8,6.6-14.8,14.8v32.9c0,8.2,6.6,14.8,14.8,14.8h48.8   c3.7,12,6.8,24.2,11.5,35.7c24.7,59.6,66.1,102,128.4,122.2c51.5,16.7,103.4,16.2,155.3,1.9c13.5-3.7,26.9-8.5,39.7-14.4   C445.988,450.788,449.188,441.188,444.288,429.288z" />
                        </g>
                        <script xmlns="" />
                      </svg>
                    </li>
                  ) : (
                    ""
                  )}

                  {pop?.info?.second_hand ? (
                    <li>
                      Etat: <span>Occasion</span>
                    </li>
                  ) : pop?.info ? (
                    <li>
                      Etat: <span>Neuf</span>
                    </li>
                  ) : (
                    ""
                  )}

                  {pop?.info?.date ? (
                    <li>
                      Date:{" "}
                      <span>
                        {new Date(pop.info.date).toLocaleDateString()}
                      </span>
                    </li>
                  ) : (
                    ""
                  )}
                  {pop?.info?.who ? (
                    <li>
                      Par: <span>{pop.info.who}</span>
                    </li>
                  ) : (
                    ""
                  )}
                </ul>
              ) : (
                ""
              )}
              <ul>
                {hbdb_data?.date ? (
                  <li>
                    <div className="galery" onClick={switchImgsPls}>
                      <img src={hbdb_imgs[img_index]} />
                      <span>{img_index + 1 + " / " + hbdb_imgs.length}</span>
                    </div>
                  </li>
                ) : (
                  ""
                )}
                {hbdb_data?.date ? (
                  <li>Date de sortie: {hbdb_data?.date}</li>
                ) : (
                  ""
                )}
                {hbdb_data?.productionFields?.find(f => f.name == "Made In") ? (
                  <li>
                    Lieux de fabrication:{" "}
                    {
                      hbdb_data?.productionFields?.find(
                        f => f.name == "Made In"
                      ).data[0].value
                    }
                  </li>
                ) : (
                  ""
                )}
                {pop?.hdbid ? (
                  <li>
                    <a
                      href={
                        "https://www.hobbydb.com/marketplaces/poppriceguide/catalog_items/" +
                        pop?.hdbid
                      }
                      target="_blank"
                    >
                      {" "}
                      Voir la page poppriceguide{" "}
                    </a>
                  </li>
                ) : (
                  ""
                )}
              </ul>
              {!pop?.info ? (
                <ul>
                  <li>
                    Valeur:{" "}
                    <span>{Math.floor(pop.estimated / window.$rate)}</span>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      x="0px"
                      y="0px"
                      viewBox="0 0 481.569 481.569"
                    >
                      <g>
                        <path d="M444.288,429.288c-5.4-13.2-10.9-26.4-16.9-39.4c-5.3-11.6-12.1-15-24.8-12.1c-16.1,3.7-31.9,8.6-48,11.9   c-31.1,6.5-62.3,7.1-93-2.6c-38.5-12.1-59-40-71.6-76h104.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-114.4   c0-9.2-0.1-18,0-26.8h114.4c8.2,0,14.8-6.6,14.8-14.8v-32.9c0-8.2-6.6-14.8-14.8-14.8h-100c0-0.4,0-0.8,0.2-1   c12-27.3,29.5-49.2,58.2-60.6c33.4-13.2,67.5-12.9,101.9-5.8c16.3,3.3,32.3,8.3,48.6,12c11.9,2.7,18.8-0.8,23.9-11.9   c5.9-12.8,11.3-25.8,16.7-38.9c5.1-12.3,2.1-21-9.5-27.8c-2.9-1.7-5.9-3.1-9-4.3c-48.2-18.8-97.9-25.8-149.2-17.6   c-36.1,5.8-69.8,18.2-98.9,40.8c-36.7,28.4-60.5,65.9-74.3,110l-1.7,5.1h-51.4c-8.2,0-14.8,6.6-14.8,14.8v32.9   c0,8.2,6.6,14.8,14.8,14.8h40.5c0,9,0,17.7,0,26.8h-40.5c-8.2,0-14.8,6.6-14.8,14.8v32.9c0,8.2,6.6,14.8,14.8,14.8h48.8   c3.7,12,6.8,24.2,11.5,35.7c24.7,59.6,66.1,102,128.4,122.2c51.5,16.7,103.4,16.2,155.3,1.9c13.5-3.7,26.9-8.5,39.7-14.4   C445.988,450.788,449.188,441.188,444.288,429.288z" />
                      </g>
                      <script xmlns="" />
                    </svg>
                  </li>
                </ul>
              ) : (
                ""
              )}
            </Card>
          </div>
        ) : (
          ""
        )}

        <div className="popgraph">
          <Card tilt={0.5} glare={0.2} scale={1.01}>
            <PopGraph labels={chart_labels} pop={pop} data={chart_data} />
          </Card>
        </div>
      </div>
    </div>
  );
}

export default Pop;
