import { NavLink } from "react-router-dom";
import VanillaTilt from "vanilla-tilt";
import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";

function Header() {
  let location = useLocation().pathname.split("/");
  location.pop();
  location = location.join("/");

  return (
    <header id="header">
      <nav>
        <ul>
          {location.split("/").length > 3 ? (
            <li>
              <NavLink to={location}>🠜 Retour</NavLink>
            </li>
          ) : (
            ""
          )}
          <li>
            <NavLink
              className={({ isActive }) => (isActive ? "active" : "")}
              to="funko/search"
            >
              Recherche{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <NavLink
              className={({ isActive }) => (isActive ? "active" : "")}
              to="funko/collection/categories"
            >
              Nos collections{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <NavLink
              className={({ isActive }) => (isActive ? "active" : "")}
              to="funko/collection/uncategorized"
            >
              Toutes nos pops{" "}
            </NavLink>{" "}
          </li>{" "}
          <li>
            <NavLink
              className={({ isActive }) => (isActive ? "active" : "")}
              to="funko/collection/statistics"
            >
              Statistiques{" "}
            </NavLink>{" "}
          </li>{" "}
        </ul>{" "}
      </nav>{" "}
    </header>
  );
}

export default Header;
