import VanillaTilt from "vanilla-tilt";
import React, { useEffect } from "react";

function SearchCard({ pop }) {
  useEffect(() => {
    VanillaTilt.init(document.querySelectorAll(".tiltcard"), {
      reverse: true,
      reset: true,
      glare: true,
      scale: 1.025,
      "max-glare": 0.1,
      "max-glare": 0.25,
      "max-glare": 0.33,
      max: 2
    });
  }, []);

  return (
    <article className="searchcard">
      <div data-tilt className="tiltcard">
        <div className="searchimage">
          <img src={pop?.attributes.images.search_url} />
        </div>
        <div className="searchinfo">
          <p>{pop?.attributes.name.toUpperCase()}</p>
          <p>#{pop?.attributes?.ref_number}</p>
          <p>
            Valeur estimée:{" "}
            {pop?.attributes.estimated_value_range
              ? Math.floor(pop?.attributes.estimated_value_range[0]) +
                " à " +
                Math.floor(pop?.attributes.estimated_value_range[1])
              : Math.floor(pop?.attributes.estimated_value)}{" "}
            $
          </p>
          <p>
            {pop?.attributes.catalog_item_type_name} |{" "}
            {pop?.attributes.series[0]?.name}
          </p>
        </div>
      </div>
    </article>
  );
}

export default SearchCard;
