import { useParams } from "react-router-dom";
import PopCard from "../components/PopCard";
import { Link } from "react-router-dom";

function Category({ pops }) {
  const collection = useParams().collection_id;
  const convertToUrlPls = window.$convertToUrlPls;
  const convertPopToIdPls = window.$convertPopToIdPls;

  if (!pops) return;

  pops = pops
    .filter(pop => convertToUrlPls(pop.collection) == collection)
    // .sort((pop) => !pop.more_details?.includes("love"))
    .sort((a, b) => b.estimated - a.estimated);

  return (
    <div id="category">
      <div>
        {pops.filter(pop => pop.ok).map((pop, i) => (
          <Link
            to={
              "/funko/collection/categories/" +
              collection +
              "/" +
              convertPopToIdPls(pop, true)
            }
            onClick={() => {document.querySelector("header").scrollIntoView()}}
            key={"popcard_" + i}
          >
            <PopCard pop={pop} />
          </Link>
        ))}
      </div>
      <div>
        {pops.filter(pop => !pop.ok).map((pop, i) => (
          <Link
            to={
              "/funko/collection/categories/" +
              collection +
              "/" +
              convertPopToIdPls(pop, true)
            }
            key={"popcard_" + i}
            onClick={() => {document.querySelector("header").scrollIntoView()}}
          >
            <PopCard pop={pop} />
          </Link>
        ))}
      </div>
    </div>
  );
}

export default Category;
