import VanillaTilt from "vanilla-tilt";
import React, { useEffect } from "react";

function Card(props) {
  let query = ".tilt";
  useEffect(() => {
    VanillaTilt.init(document.querySelectorAll(query), {
      reverse: true,
      reset: true,
      glare: true,
      scale: props.scale,
      "max-glare": props.glare,
      max: props.tilt
    });
  }, []);

  return (
    <section className={"card"}>
      <div className="notilt">{props.notilt}</div>
      <div className={props.c ? "tilt " + props.c : "tilt"} data-tilt>
        {props.children}
      </div>
    </section>
  );
}

export default Card;
