import CollectionCard from "../components/CollectionCard";
import Card from "../components/Card";
import { Link } from "react-router-dom";

function Categories({ collections, pops }) {
  return (
    <div id="categories">
      {collections.map((collection, i) => (
        <Link
          to={
            "/funko/collection/categories/" +
            window.$convertToUrlPls(collection.name)
          }
          key={"collectioncard_" + i}
          onClick={() => {document.querySelector("header").scrollIntoView()}}
        >
          <CollectionCard
            collection={collection}
            pops={pops.filter(pop => pop.collection == collection.name)}
          />
        </Link>
      ))}
    </div>
  );
}

export default Categories;
